import { createApp } from 'vue'
import App from './App.vue'
import "amfe-flexible"

import {
     Button, Icon, Tab, Tabs, TreeSelect, Stepper, ActionBar,ContactCard,
     ActionBarIcon, ActionBarButton, Checkbox, CheckboxGroup, SubmitBar,Card,
     Dialog,AddressList,AddressEdit,Form, Field, CellGroup 
} from 'vant';
import "vant/lib/index.css"

import router from './router';
import "./common/css/base.less"

import store from './store';

createApp(App).use(router).use(Button).use(Icon).use(Tab).use(Tabs)
     .use(TreeSelect).use(Stepper).use(ActionBar).use(ActionBarIcon)
     .use(ActionBarButton).use(store).use(Checkbox).use(SubmitBar)
     .use(CheckboxGroup).use(ContactCard).use(Card).use(Dialog)
     .use(AddressList).use(AddressEdit).use(Form).use(Field).use(CellGroup).mount('#app')
